import { uniqBy } from 'lodash'
import { Api, useApi } from './Api'

export const useAccumulateApiItems = <T>(
  fetchCall: (api: Api) => (offset: number, limit: number) => Promise<{ records: T[]; nextOffset: number }>,
  pageSize: number,
  onPage: (page: T[]) => void
): ((from: number, to: number) => void) => {
  const api = useApi()

  const accumulateItems = (from: number, to: number) =>
    fetchCall(api)(from, Math.min(pageSize, to - from)).then(({ records, nextOffset }) => {
      onPage(records)
      if (nextOffset && nextOffset < to) return accumulateItems(nextOffset, to)
    })

  return accumulateItems
}

export const makeOnPageHandler =
  <T>(setState: (updater: (existing: T[]) => T[]) => void) =>
  (incoming: T[]): void => {
    setState((existing: T[]) => uniqBy([...incoming, ...existing], 'id'))
  }

export const deNull = <T>(source: T, defaults: Partial<T>): T => {
  const copy = { ...source }
  Object.entries(copy).forEach(([key, value]) => {
    if (value !== null || typeof defaults[key] === 'undefined') return
    copy[key] = defaults[key]
  })
  return copy
}
