/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiInputLabel from '@mui/material/InputLabel'
import MuiMenuItem from '@mui/material/MenuItem'
import MuiFormControl from '@mui/material/FormControl'
import MuiSelect, { SelectChangeEvent as MuiSelectChangeEvent } from '@mui/material/Select'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

export interface SelectFieldProps extends CommonFieldProps<string | number> {
  options: {
    value: string | number
    label: React.ReactNode
    disabled?: boolean
  }[]
}

export const SelectField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  readOnly,
  value,

  // SelectFieldProps
  options,
}: SelectFieldProps): JSX.Element => {
  if (readOnly) {
    const valueLabel = options.find((option) => option.value === value)?.label ?? ''
    return <ReadOnlyField className={className} gridArea={gridArea} label={label} value={valueLabel} />
  }

  const handleChange = ({ target: { value: newValue } }: MuiSelectChangeEvent<string | number>) => {
    onChange(newValue)
  }

  return (
    <MuiFormControl
      sx={{ minWidth: '10em' }}
      className={className}
      disabled={disabled || readOnly}
      css={{ '.MuiInputBase-root': { color: 'unset' }, gridArea }}
    >
      {label && (
        <MuiInputLabel
          // size="small" // typing says this isn't a prop, but it works to correctly position label :-|
          error={error}
        >
          {label}
        </MuiInputLabel>
      )}
      <MuiSelect<string | number> error={error} label={label} onChange={handleChange} value={value} size="small">
        {options.map((option) => (
          <MuiMenuItem key={option.value} value={option.value} disabled={option.disabled}>
            {option.label || <span css={{ fontStyle: 'italic' }}>(clear)</span>}
          </MuiMenuItem>
        ))}
      </MuiSelect>
    </MuiFormControl>
  )
}
